<template>
  <div id="app" class="container">
    <br>
    <div class="jumbotron jumbotron-fluid">
      <div class="row">
        <div class="col-4 align-content-center">
          <img class="mx-auto d-block" src="./assets/github.png" alt="logo github" style="width: 200px">
        </div>
      </div>
      <div class="text-center">
        <h3 class="display-4">Actualizar Repositorio</h3>
        <b class="lead" :class="color">{{texto}}</b><br>
        <b>Sebastian Robalino</b>
      </div>
      <br>
      <div v-if="cargando">
        <div class="row container align-content-center">
          <div class="col"><button type="button" class="btn btn-success btn-lg btn-block" v-on:click="consultaWEB">Servidor de Páginas</button></div>
          <div class="col"><button type="button" class="btn btn-info btn-lg btn-block" v-on:click="consultaAPI">Servidor de Servicios Web</button></div>
        </div>
        <br>
        <div class="col" v-if="respuesta">
          <div class="card">
            <div class="card-body" style="white-space: pre;">{{respuesta}}</div>
          </div>
        </div>
      </div>
      <div v-else>
        <div class="progress">
          <div class="progress-bar progress-bar-striped progress-bar-animated" role="progressbar" aria-valuenow="100" aria-valuemin="0" aria-valuemax="100" style="width: 100%"></div>
        </div>
        <div class="text-center">
          Procesando
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'App',
    data: () => ({
      datos: null,
      pasos:0,
      cargando:true,
      respuesta:null,
      //localhost:1005
      urlApi:'http://www.puceing.edu.ec:15005/srobalino/GitHub/actualizar.asmx',
      urlApiRes:'http://www.puceing.edu.ec:15005/srobalino/github.txt',
      urlWeb:'http://www.puceing.edu.ec:15006/srobalino/GitHub/actualizar.asmx',
      urlWebRes:'http://www.puceing.edu.ec:15006/srobalino/github.txt',
      cabApi:'<?xml version="1.0" encoding="utf-8"?>\n' +
        '<soap12:Envelope xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:xsd="http://www.w3.org/2001/XMLSchema" xmlns:soap12="http://www.w3.org/2003/05/soap-envelope">\n' +
        '  <soap12:Body>\n' +
        '    <Ejecutar xmlns="http://tempuri.org/" />\n' +
        '  </soap12:Body>\n' +
        '</soap12:Envelope>',
      cabWeb:'<?xml version="1.0" encoding="utf-8"?>\n' +
        '<soap12:Envelope xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:xsd="http://www.w3.org/2001/XMLSchema" xmlns:soap12="http://www.w3.org/2003/05/soap-envelope">\n' +
        '  <soap12:Body>\n' +
        '    <Ejecutar xmlns="http://tempuri.org/" />\n' +
        '  </soap12:Body>\n' +
        '</soap12:Envelope>',
    }),
    computed: {
      color: function () {
        let nueva = 'text-hide';
        if (location.port === '15006') { nueva = 'text-success' } else if (location.port === '15005') { nueva = 'text-info' } else { nueva = 'text-danger' }
        return nueva
      },
      texto: function () {
        let nueva = 'No esta en el servidor ISS';
        if (location.port === '15006') { nueva = 'Servidor de Páginas' }
        if (location.port === '15005') { nueva = 'Servidor de Servicios Web' }
        return nueva
      }
    },
    methods: {
      consultaWEB: function () {
        this.cargando=false;
        let vm=this;
        axios.defaults.headers.post['Content-Type'] = 'text/xml';
        axios.post(this.urlWeb,this.cabWeb)
          .then(res=>{
            axios.get(this.urlWebRes).then(res2=>{
              vm.respuesta=res2.data;
              vm.cargando=true;
            });
          }).catch(err=>{
            vm.respuesta=err;
          vm.cargando=true;
        });
      },
      consultaAPI: function () {
        this.cargando=false;
        let vm=this;
        axios.defaults.headers.post['Content-Type'] = 'text/xml';
        axios.post(this.urlApi,this.cabApi)
          .then(res=>{
            axios.get(this.urlApiRes).then(res2=>{
              vm.respuesta=res2.data;
              vm.cargando=true;
            });
          }).catch(err=>{
          vm.respuesta=err;
          vm.cargando=true;
        });
      }
    }
  }
</script>

<style>
  @import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
</style>



